/* Pointer ring */

#cursor {
  position: fixed;
  top: -5px;
  left: -5px;
  width: 10px;
  height: 10px;
  background-color: var(--secondary);
  border-radius: 50%;
  pointer-events: none;
  z-index: 100000;
  box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.2);
}

#cursor-border {
  --size: 30px;
  position: fixed;
  top: calc(var(--size) / -2);
  left: calc(var(--size) / -2);
  width: var(--size);
  height: var(--size);
  border-radius: 50%;
  box-shadow: 0 0 0 2px var(--primary);
  pointer-events: none;
  transition: top 0.15s ease-out, left 0.15s ease-out, width 0.15s ease-out, height 0.15s ease-out, background-color 0.15s ease-out;
  z-index: 100000;
}

/* Sticky header  */

.site-header {
  position: absolute;
}

.site-header.is-fixed {
  position: fixed !important;
  background-color: #fff;
  transition: all 0.8s;
  box-shadow: 0 0 50px 0 rgba(0, 0, 0, 0.2);
  animation: slidedown 0.8s ease;
}

.site-header.is-fixed .main-bar {
  padding: 0;
}

.site-header.is-fixed .extra-cell .white-btn {
  background-color: var(--primary) !important;
  color: #fff!important;
}

/* #Progress
================================================== */

.progress-wrap {
  position: fixed;
  cursor: pointer;
  bottom: 15px;
  right: 15px;
  width: 50px;
  height: 50px;
  border-radius: 50px;
  display: block;
  opacity: 0;
  box-shadow: inset 0 0 0 2px rgba(0, 0, 0, 0.2);
  z-index: 999;
  visibility: hidden;
  transform: translateY(20px);
  transition: all 200ms linear;
}

.progress-wrap:after {
  content: "\f062";
  font-size: 18px;
  font-family: "Font Awesome 6 Free";
  font-weight: 900;
  position: absolute;
  left: 0;
  top: 0;
  height: 50px;
  cursor: pointer;
  width: 50px;
  line-height: 50px;

  text-align: center;
  transition: all 200ms linear;
}

.progress-wrap.active-progress {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.progress-wrap > svg {
  fill: none;
}

.progress-wrap > svg.progress-circle path {
  stroke: var(--primary);
  stroke-width: 4;
  box-sizing: border-box;
  transition: all 200ms linear;
}

