/* Animations  */

/* letters-loading */

@keyframes letters-loading{
  0%,
	75%,
	100% {
    opacity: 0;
    transform: rotateY(-90deg);
  }

  25%,
	50% {
    opacity: 1;
    transform: rotateY(0deg);
  }
}

/* Slidedown */

@keyframes slidedown{
  0%{
    margin-top: -150px;
  }

  100%{
    margin-top: 0px;
  }
}

/* logo Animation */

@keyframes swigo-logo {
  from,
	45%,
	55%,
	65%,
	75%,
	80%,
	85%,
	to{
    animation-timing-function: cubic-bezier(0.200, 0.600, 0.350, 1.000);
  }

  0%{
    transform: rotate(0);
  }

  45%{
    transform: translate3d(-4%, -13%, 0) rotate(-13deg);
  }

  55%{
    transform: translate3d(1%, -2%, 0) rotate(6deg);
  }

  65%{
    transform: translate3d(-2%, -4%, 0) rotate(-3deg);
  }

  75%{
    transform: translate3d(2%, 3%, 0) rotate(3deg);
  }

  80%{
    transform: translate3d(-2%, 1%, 0) rotate(-2deg);
  }

  to{
    transform: none;
  }
}

/* Shake Animation  */

@keyframes shake {
  0% {
    transform: translate(1px, 1px) rotate(0deg);
  }

  10% {
    transform: translate(-1px, -2px) rotate(-1deg);
  }

  20% {
    transform: translate(-3px, 0px) rotate(1deg);
  }

  30% {
    transform: translate(3px, 2px) rotate(0deg);
  }

  40% {
    transform: translate(1px, -1px) rotate(1deg);
  }

  50% {
    transform: translate(-1px, 2px) rotate(-1deg);
  }

  60% {
    transform: translate(-3px, 1px) rotate(0deg);
  }

  70% {
    transform: translate(3px, 1px) rotate(-1deg);
  }

  80% {
    transform: translate(-1px, -1px) rotate(1deg);
  }

  90% {
    transform: translate(1px, 2px) rotate(0deg);
  }

  100% {
    transform: translate(1px, -2px) rotate(-1deg);
  }
}

/* Rotate */

@keyframes dzRotate360{
  0%{
    transform: rotate(0deg);
  }

  100%{
    transform: rotate(360deg);
  }
}

/* upToDown */

@keyframes upToDown{
  49%{
    transform: translateY(100%);
  }

  50%{
    opacity: 0;
    transform: translateY(-100%);
  }

  51%{
    opacity: 1;
  }
}

/* shine */

@keyframes shine {
  100% {
    left: 125%;
  }
}

/* MoveScaleUpInitial */

@keyframes MoveScaleUpInitial{
  to{
    transform: translate3d(0, -105%, 0) scale3d(1, 2, 1);
    opacity: 0;
  }
}

/* MoveScaleUpEnd */

@keyframes MoveScaleUpEnd{
  from{
    transform: translate3d(0, 100%, 0) scale3d(1, 2, 1);
    opacity: 0;
  }

  to{
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

@keyframes move2 {
  0% {
    transform: rotate(1deg) translate(2px, 2px);
  }

  50% {
    transform: rotate(-1deg) translate(-2px, -2px);
  }

  100% {
    transform: rotate(1deg) translate(2px, 2px);
  }
}

/* zoomLoader */

@keyframes zoomLoader{
  0%{
    transform: scale(1);
    opacity: 1;
  }

  15%{
    transform: scale(5);
  }

  25%{
    transform: scale(9);
  }

  35%{
    transform: scale(12);
  }

  45%{
    transform: scale(15);
  }

  55%{
    transform: scale(18);
  }

  65%{
    transform: scale(18) translateX(-20%);
  }

  75%{
    transform: scale(18) translateX(-40%);
  }

  85%{
    transform: scale(18) translateX(-60%);
  }

  95%{
    transform: scale(18) translateX(-80%);
    opacity: 1;
  }

  100%{
    transform: scale(18) translateX(-100%);
  }
}

