.popup-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 50;
}

.popup-content {

  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
 
}

.slider {
  position: relative;
  width: 100%;
  overflow: hidden;
}

.slider img {
  
  border-radius: 0.5rem;
}

.slider button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  cursor: pointer;
  z-index: 1;
}

.slider button.left {
  left: 10px;
}

.slider button.right {
  right: 10px;
}
